/* Large devices (laptops/desktops, 992px and up) */
@media only screen and (min-width: 992px) {
  .clients-list{
    display: flex;
    justify-content: center;
    img{
      width: 100%;
      height: auto;
      padding: 5px;
    }
  }
  .welcome{
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: center;
    .w-block{
      max-width: 40vw;
      padding: 25px;
    }
  }
  .signup-input{
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: center;
  .s-input{
    width: 45vw;
    padding: 5px;
  }


}
  .nav-btm{
    display: block;
  }





}
