/* Medium devices (landscape tablets, 768px and up) */
@media only screen and (min-width: 768px) {
  .clients-list{
    display: flex;
    justify-content: center;
    img{
      width: 100%;
      height: auto;
      padding: 5px;
    }
  }
  .news-articles{
    display: flex;
    flex-direction: row;
    justify-content: center;
    .news-block{
      width: 45%;
      margin: 15px;
      }
  }





}
